import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import CssBaseline from '@material-ui/core/CssBaseline';
import { withStyles } from '../../materialLib';
import Sidebar from '../Sidebar';
import withUser from '../../actions/withUser';
import { closeNotifier } from '../../actions/notifier';
import withNotifier from '../../actions/withNotifier';
import Notifier from '../Notifier';

const styles = theme => ({
  root: {
    display: 'flex',
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing.unit,
    height: '100vh',
    backgroundColor: theme.palette.background.paper,
  },
  toolbar: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'flex-end',
    padding: '0 8px',
    minHeight: '40px',
    ...theme.mixins.toolbar,
  },
});

class Layout extends React.Component {
  static propTypes = {
    showSidebar: PropTypes.bool,
    user: PropTypes.object,
    children: PropTypes.node.isRequired,
    classes: PropTypes.object.isRequired,
    theme: PropTypes.object.isRequired,
    closeNotifier: PropTypes.func,
    notifier: PropTypes.object,
  };

  static defaultProps = {
    showSidebar: true,
    user: null,
    notifier: {},
    closeNotifier: () => {},
  };

  state = {
    open: false,
  };

  componentDidMount() {
    const canUseDOM = !!(
      typeof window !== 'undefined' &&
      window.document &&
      window.document.createElement
    );

    // Don't load these styles server-side
    if (canUseDOM) {
      // Import the antThemeLoader.less file for hot reloading theme changes
      require('components/antThemeLoader.less'); // eslint-disable-line global-require
    }
  }

  handleDrawerOpen = () => {
    this.setState({ open: true });
  };

  handleDrawerClose = () => {
    this.setState({ open: false });
  };

  closeNotifier = () => {
    this.props.closeNotifier({ openNotify: false });
  };

  render() {
    const { user, showSidebar, classes, notifier } = this.props;
    const { open } = this.state;
    return (
      <div className={classes.root}>
        <CssBaseline />
        {showSidebar ? (
          <Sidebar
            user={user}
            open={open}
            handleDrawerClose={this.handleDrawerClose}
            handleDrawerOpen={this.handleDrawerOpen}
          />
        ) : null}
        <main
          className={classes.content}
          style={!showSidebar ? { background: '#1b144e' } : {}}
        >
          {this.props.children}
        </main>
        <Notifier
          open={notifier && notifier.openNotify}
          handleClose={this.closeNotifier}
          message={notifier && notifier.message}
          variant={notifier && notifier.variant}
        />
      </div>
    );
  }
}

export default connect(null, {
  closeNotifier,
})(withNotifier(withUser(withStyles(styles, { withTheme: true })(Layout))));
