/* eslint-disable import/no-extraneous-dependencies */
import React, { useState } from 'react';
import PropTypes from 'prop-types';
import _ from 'lodash';
import {
  MenuItem,
  Menu,
  Help,
  Tooltip,
  ListItem,
  ListItemText,
  ListItemIcon,
  Button,
  withStyles,
} from '../../materialLib';
import { STATIC_URLS } from '../../constants';

const styles = theme => ({
  root: {
    width: '100%',
  },
  grow: {
    flexGrow: 1,
  },
  menuButton: {
    marginLeft: -12,
    marginRight: 20,
  },
  title: {
    display: 'none',
    [theme.breakpoints.up('sm')]: {
      display: 'block',
    },
  },
  inputRoot: {
    color: 'inherit',
    width: '100%',
  },
  inputInput: {
    paddingTop: theme.spacing.unit,
    paddingRight: theme.spacing.unit,
    paddingBottom: theme.spacing.unit,
    paddingLeft: theme.spacing.unit * 10,
    transition: theme.transitions.create('width'),
    width: '100%',
    [theme.breakpoints.up('md')]: {
      width: 200,
    },
  },
  sectionDesktop: {
    display: 'none',
    [theme.breakpoints.up('md')]: {
      display: 'flex',
    },
  },
  sectionMobile: {
    display: 'flex',
    [theme.breakpoints.up('md')]: {
      display: 'none',
    },
  },
  link: {
    textDecoration: 'none',
  },
  textLabel: {
    color: '#fff',
  },
});

const HelpComponent = props => {
  const { classes } = props;
  const [anchorEl, setAnchorEl] = useState(null);

  const handleMenuClose = () => setAnchorEl(() => null);

  const handleMenu = event => setAnchorEl(() => event.currentTarget);

  const handleRedirection = item => {
    const currentUrl = _.isEqual(item.label, 'API')
      ? `${window.location.host}${item.url}`
      : `${item.url}`;
    window.open(currentUrl, '_blank');
    setAnchorEl(() => null);
  };

  const isMenuOpen = Boolean(anchorEl);
  return (
    <div>
      <Button
        style={{ textDecoration: 'none', padding: 0 }}
        key="help"
        onClick={handleMenu}
      >
        <ListItem button key="help" style={{ textTransform: 'capitalize' }}>
          <ListItemIcon style={{ margin: '0 8px' }}>
            <Help style={{ color: '#fff' }} />
          </ListItemIcon>
          <ListItemText
            classes={{ primary: classes.textLabel }}
            primary="Help"
          />
        </ListItem>
      </Button>
      <Menu
        id="menu-appbar"
        anchorEl={anchorEl}
        anchorOrigin={{
          vertical: 'top',
          horizontal: 'right',
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'right',
        }}
        open={isMenuOpen}
        onClose={handleMenuClose}
      >
        {_.map(STATIC_URLS, item => {
          return _.isEqual(item.label, 'API') ? (
            <Tooltip key={item.label} title={`${item.info}`} placement="top">
              <a
                style={{ textDecoration: 'none' }}
                target="_blank"
                rel="noreferrer noopener"
                href={`${window.location.host}${item.url}`}
              >
                <MenuItem>{item.label}</MenuItem>
              </a>
            </Tooltip>
          ) : (
              <Tooltip key={item.label} title={`${item.info}`} placement="top">
                <a
                  style={{ textDecoration: 'none' }}
                  target="_blank"
                  rel="noreferrer noopener"
                  onClick={() => handleRedirection(item)}
                >
                  <MenuItem>{item.label}</MenuItem>
                </a>
              </Tooltip>
            );
        })}
      </Menu>
    </div>
  );
};

HelpComponent.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(HelpComponent);
