/* eslint-disable react/prop-types */
/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useState } from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { connect } from 'react-redux';
import _ from 'lodash';
import { logout } from '../../actions/user';
import {
  setGraphTableData,
  fetchGraph,
  setRepositories,
} from '../../actions/graph';
import { clearThemeState } from '../../actions/theme';
import { clearHomeState } from '../../actions/home';
import { clearUsermanagementState } from '../../actions/userManagement';
import { clearNotifierState, openNotifier } from '../../actions/notifier';
import history from '../../history';
import { API_URLS } from '../../constants';
import {
  Drawer,
  List,
  IconButton,
  ChevronLeftIcon,
  ChevronRightIcon,
  ListItem,
  ListItemIcon,
  ListItemText,
  MenuItem,
  Menu,
  MenuIcon,
  Toolbar,
  AccountCircle,
  withStyles,
} from '../../materialLib';
import Link from '../Link';
import Icon from '../Icon';
import HelpComponent from '../HelpComponent';
import logoUrl from '../Header/Syndeia-logo.png';
import graphIcon from './graph.png';
import withNotifier from '../../actions/withNotifier';
import withUser from '../../actions/withUser';

const drawerWidth = 240;

function IconWrapper({ icon }) {
  return <img src={icon} style={{ width: 22 }} alt={icon} />;
}

const styles = theme => ({
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
    whiteSpace: 'nowrap',
    backgroundColor: '#1b144E',
    color: '#fff',
    [theme.breakpoints.up('sm')]: {
      width: drawerWidth,
      flexShrink: 0,
    },
  },
  drawerOpen: {
    backgroundColor: '#1b144E',
    width: drawerWidth,
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
    color: '#fff',
  },
  drawerClose: {
    backgroundColor: '#1b144E',
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    overflowX: 'hidden',
    width: theme.spacing.unit * 7 + 1,
    [theme.breakpoints.up('sm')]: {
      width: theme.spacing.unit * 9 + 1,
    },
    color: '#fff',
  },
  toolbar: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'flex-end',
    padding: '0 8px',
    ...theme.mixins.toolbar,
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing.unit * 3,
  },
  textLabel: {
    color: '#fff',
  },
  menuButton: {
    marginRight: 8,
    padding: '0 8px',
    [theme.breakpoints.up('sm')]: {
      display: 'block',
    },
  },
  hide: {
    display: 'none',
  },
});

const Sidebar = (props, context) => {
  const [anchorEl, setAnchorEl] = useState(null);

  const handleLogout = async () => {
    try {
      const response = await context.fetch(`${API_URLS.signOut}`);
      const data = await response.json();
      if (response.status === 200) {
        await props.openNotifier({
          openNotify: true,
          message: data.resources,
          variant: 'success',
        });
      } else if (response.status === 400) {
        await props.openNotifier({
          openNotify: true,
          message: data.resources,
          variant: 'error',
        });
      }
      await props.setGraphTableData({
        graphTableEdges: [],
        graphTableNodes: [],
      });
      await props.fetchGraph({
        edges: [],
        vertices: [],
      });
      await props.clearThemeState();
      await props.clearHomeState();
      await props.clearUsermanagementState();
      await props.clearNotifierState();
      await props.logout();
      await setAnchorEl(() => null);
      await props.setRepositories({
        orphanKeys: [],
        repositories: [],
        cachedRepos: {},
      });
      history.push('');
    } catch (e) {
      console.error(e);
    }
  };

  const handleMenuClose = () => {
    setAnchorEl(() => null);
  };

  const handleMenu = event => {
    setAnchorEl(() => event.currentTarget);
  };

  const redirectToProfile = () => {
    history.push(`/my-profile/${_.get(user, 'userId')}`);
    setAnchorEl(() => null);
  };

  const {
    classes,
    theme,
    open,
    handleDrawerClose,
    user,
    handleDrawerOpen,
  } = props;
  const isMenuOpen = Boolean(anchorEl);

  const getBaseUrls = () => [
    {
      label: 'Graph Queries',
      url: '/graph-visual',
      icon: 'graphicEq',
      isAuthorised:
        _.includes(user.role, 'Super Admin') ||
        _.includes(user.role, 'Admin') ||
        _.includes(user.role, 'User'),
    },
    {
      label: 'Users',
      url: '/user-management',
      icon: 'userManagement',
      isAuthorised:
        _.includes(user.role, 'Super Admin') || _.includes(user.role, 'Admin'),
    },
  ];

  return (
    <Drawer
      variant="permanent"
      className={classNames(classes.drawer, {
        [classes.drawerOpen]: open,
        [classes.drawerClose]: !open,
      })}
      classes={{
        paper: classNames({
          [classes.drawerOpen]: open,
          [classes.drawerClose]: !open,
        }),
      }}
      open={open}
    >
      <div className={classes.toolbar}>
        <Toolbar disableGutters={!open}>
          {open ? (
            <IconButton style={{ color: '#fff' }} onClick={handleDrawerClose}>
              {theme.direction === 'rtl' ? (
                <ChevronRightIcon />
              ) : (
                  <ChevronLeftIcon />
                )}
            </IconButton>
          ) : (
              <IconButton
                style={{ color: '#fff' }}
                aria-label="Open drawer"
                onClick={handleDrawerOpen}
                className={classNames(classes.menuButton, {
                  [classes.hide]: open,
                })}
              >
                <MenuIcon />
              </IconButton>
            )}
        </Toolbar>
      </div>
      <List>
        <Link to="/home" style={{ textDecoration: 'none' }}>
          <ListItem style={{ textTransform: 'capitalize' }} button key="home">
            <ListItemIcon style={{ margin: '0 8px 0 5px' }}>
              <img
                src={logoUrl}
                alt="logo"
                style={{
                  height: 30,
                  verticalAlign: 'middle',
                  borderStyle: 'none',
                  display: 'block',
                }}
              />
            </ListItemIcon>
            <ListItemText
              classes={{ primary: classes.textLabel }}
              primary="Home"
            />
          </ListItem>
        </Link>
        {user &&
          _.map(
            getBaseUrls(),
            module =>
              module.isAuthorised && (
                <Link
                  to={module.url}
                  style={{ textDecoration: 'none' }}
                  key={module.label}
                >
                  <ListItem button key={module.label}>
                    <ListItemIcon
                      style={
                        _.isEqual(module.icon, 'graphicEq')
                          ? { margin: '0 10px 0 8px', height: 22 }
                          : { margin: '0 8px' }
                      }
                    >
                      {_.isEqual(module.icon, 'graphicEq') ? (
                        <IconWrapper icon={graphIcon} />
                      ) : (
                          <Icon iconName={module.icon} />
                        )}
                    </ListItemIcon>
                    <ListItemText
                      classes={{ primary: classes.textLabel }}
                      primary={module.label}
                    />
                  </ListItem>
                </Link>
              ),
          )}
        <HelpComponent />
        {user ? (
          <>
            <Link
              to="#"
              style={{ textDecoration: 'none', padding: 0 }}
              key="profile"
              onClick={handleMenu}
            >
              <ListItem
                style={{ textTransform: 'capitalize' }}
                button
                key="profile"
              >
                <ListItemIcon style={{ margin: '0 8px' }}>
                  <AccountCircle style={{ color: '#fff' }} />
                </ListItemIcon>
                <ListItemText
                  classes={{ primary: classes.textLabel }}
                  primary="My Profile"
                />
              </ListItem>
            </Link>
          </>
        ) : null}
      </List>
      <Menu
        id="menu-appbar"
        anchorEl={anchorEl}
        anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
        transformOrigin={{ vertical: 'top', horizontal: 'right' }}
        open={isMenuOpen}
        onClose={handleMenuClose}
      >
        <MenuItem onClick={redirectToProfile}>My Profile</MenuItem>
        <MenuItem onClick={handleLogout}>Logout</MenuItem>
      </Menu>
    </Drawer>
  );
};

Sidebar.propTypes = {
  classes: PropTypes.object.isRequired,
  handleDrawerClose: PropTypes.func.isRequired,
  handleDrawerOpen: PropTypes.func.isRequired,
  open: PropTypes.bool.isRequired,
  theme: PropTypes.object.isRequired,
  logout: PropTypes.func.isRequired,
  setGraphTableData: PropTypes.func.isRequired,
  fetchGraph: PropTypes.func.isRequired,
  clearThemeState: PropTypes.func.isRequired,
  clearHomeState: PropTypes.func.isRequired,
  clearUsermanagementState: PropTypes.func.isRequired,
  clearNotifierState: PropTypes.func.isRequired,
  user: PropTypes.object,
  openNotifier: PropTypes.func,
};

Sidebar.defaultProps = {
  user: {},
  openNotifier: () => { },
};

Sidebar.contextTypes = {
  fetch: PropTypes.func.isRequired,
};

export default connect(null, {
  logout,
  setGraphTableData,
  fetchGraph,
  clearThemeState,
  clearHomeState,
  clearUsermanagementState,
  clearNotifierState,
  openNotifier,
  setRepositories,
})(withNotifier(withUser(withStyles(styles, { withTheme: true })(Sidebar))));
