/* eslint-disable import/newline-after-import */
import React from 'react';
import PropTypes from 'prop-types';
import { Code, GraphicEq, Group } from '../../materialLib';
import Home from '../Header/Syndeia-logo.png';
const icons = {
  code: Code,
  graphicEq: GraphicEq,
  userManagement: Group,
  home: Home,
};

const Icon = props => {
  const TagName = icons[props.iconName];
  return <TagName style={{ color: '#fff' }} />
};

Icon.propTypes = {
  iconName: PropTypes.string.isRequired,
};

export default Icon;
