import { ACTIONS } from '../constants';

export function openNotifier(data) {
  return {
    type: ACTIONS.OPEN_NOTIFIER,
    payload: data,
  };
}

export function closeNotifier(data) {
  return {
    type: ACTIONS.CLOSE_NOTIFIER,
    payload: data,
  };
}

export function clearNotifierState() {
  return {
    type: ACTIONS.CLEAR_NOTIFIER_STATE,
    payload: null,
  };
}
